body{
  margin: 0;
  font-family: 'Spoqa Han Sans Neo', 'sans-serif'; 
}
/* ----- app --------------------------------  */
.app{
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  min-height: 100vh;
  background: #f4f4f4;
}
/* ----- top navi --------------------------------  */
.topnavi{
  display:flex;
  width:1200px;
  justify-content: space-between;
  height:100px;
  align-items: center;
  padding:20px 0px 0px 0px;
}
.topnavi > div:nth-child(1){
  display:flex;
  flex:1;
  border-bottom:solid 1px #dbdbdb;
  height:1px;
  margin:0px 20px 0px 0px;
}
.topnavi > div:nth-child(3){
  display:flex;
  flex:1;
  border-bottom:solid 1px #dbdbdb;
  height:1px;
  margin:0px 0px 0px 20px;
}
.topnavi > div:nth-child(2){
  display:flex;
  justify-content: center;
  align-items: center;
  height:80px;
  width:80px;
  overflow: hidden;
  border-radius: 20px;
}
.topnavi > div:nth-child(2) > img{
  object-fit: cover;
  width:100%;
  height:100%;
}
/* ----- top navi --------------------------------  */
.index{
  display:flex;
  width:1200px;
  align-items: center;
}
.index > div{
  display:flex;
  flex-direction: column;
  width:100%;
}
.index_lv1{
  display:flex;
  width:100%;
  justify-content: center;
  margin:-5px 0px 0px 0px;
  font-weight: bold;
  font-family: 'jua';
}
.index_lv2{
  display:flex;
  flex-direction: column;
  align-items: center;
  margin:100px 0px 0px 0px;
  font-size: 5rem;
  font-weight: bold;
  font-family: 'jua';
  /* font-family: 'do hyun'; */
}
.index_lv2 > span:first-child{
  display:flex;
  /* margin:0px 0px 50px 0px; */
}
.index_lv2{
  display:flex;
  width:100%;
  justify-content: center;
}
.index_lv2 > div{
  display:flex;
  width:150px;
  background:#7f7f7f;
  padding:10px 20px;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 2px rgba(127,127,127,0.3);
  cursor: pointer;
}
.index_lv2 > div:hover{
  transition:all 0.3s;
  background: #66c0c6;
}
.index_lv2 > div > img{
  object-fit: cover;
  width:100%;
}
/* ----- app download --------------------------------  */
.app_down{
  display:flex;
  width:1200px;
  align-items: center;
}
.app_down > div{
  display:flex;
  flex-direction: column;
  width:100%;
}
/* ----- privacy --------------------------------  */
.privacy{
  display:flex;
  width:1200px;
  align-items: center;
}
.privacy > div{
  display:flex;
  flex-direction: column;
  width:100%;
  padding:0px 0px 100px 0px;
}
.privacy_lv1{
  display:flex;
  width:100%;
  justify-content: center;
  font-size: 3rem;
  margin:50px 0px 20px 0px;
  font-weight: bold;
}
.privacy_policy{
  display:flex;
  flex-direction: column;
  width:100%;
}
.privacy_title{
  font-weight: bold;
  display:flex;
  margin:20px 0px 10px 0px;
}
.privacy_body{
  display:block;
  width:100%;
}